<template>
  <div>
    <!-- 头部 -->
    <nav-bar class="home-nav">
      <div slot="left" class="lefts" @click="$router.go(-1)">
        <img src="~assets/img/common/back.svg" alt />
      </div>
      <div slot="center" class="header">
        <img src="@/styles/img/4、微网站搜索_03.png" alt />
      </div>
    </nav-bar>

    <div class="menu">
      <a href="detail.html">
        <span>商品详情</span>
      </a>
      <a href="comment.html">
        <span style="color: #000">商品评论</span>
      </a>
      <div class="so"></div>
    </div>

    <div class="imag">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" @change="handleChange">
        <van-swipe-item v-for="(item,index) in banner" :key="index">
          <img :src="item" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="number">
      {{now}}
      <span>/{{banner.length}}</span>
    </div>

    <div class="main">
      <div class="title">
        BOSC XISTERA {{goods.name}}
        <div class="xian"></div>
      </div>
      <div class="titlebox">
        <div class="xian"></div>
        <div class="titlemain">
          <div class="left">种类</div>
          <div class="right">现代客厅单人沙发</div>
        </div>
        <div class="titlemain">
          <div class="left">规格</div>
          <div
            class="right"
            v-for="(item,index) in message"
            :key="index"
          >{{item.name}}:{{item.value}}</div>
        </div>
        <div class="titlemain">
          <div class="left">颜色</div>
          <div class="right">
            <ul class="main-hot-pro">
              <li
                v-for="(v,index) in colorData"
                :key="index"
                :style="{'border-color':v.value,background:activeColor===index ? v.value:''}"
                @click="hadleActiveColor(index)"
              ></li>
            </ul>
          </div>
        </div>
        <div class="titlemain">
          <div class="left">配送</div>
          <div class="right" v-for="item in templateT" :key="item.index">{{item.areas}}</div>
        </div>
      </div>
      <div class="titlebox2">
        <div class="num">
          <span>{{goods.price | currency}}</span>RMB
        </div>
        <div class="left">购买数量</div>
        <div class="jis">
          <div class="jisleft" @click="handleDec">-</div>
          {{count}}
          <div class="jisright" @click="handleAdd">+</div>
        </div>
      </div>
      <div class="title2">
        <div class="xian"></div>您已经
        <span>浏览</span>过的商品
      </div>
      <div class="title2-main">
        <ul class="mainbox">
          <li v-for="item in viewHistoryGoods" :key="item.id">
            <router-link :to="{}">
              <img :src="item.thumb" />
              <div class="line"></div>
              <h4>VERFO LAB</h4>
              <h2>{{item.name}}</h2>
              <h6>
                {{item.price | currency}}
                <span>RMB</span>
              </h6>
              <a href class="buy-now">
                <i class="iconfont icon-jia"></i>
                <span>点击购买</span>
              </a>
            </router-link>
          </li>
        </ul>
      </div>
    </div>

    <div class="detail">
      <div class="detail-main">
        <div class="xian1"></div>
        <div class="xian2"></div>
        <div class="xian3"></div>
        <h6>COMMODITY DETAILS</h6>
        <h3>
          <span>商品</span>
          <span>详情</span>
        </h3>
        <div v-html="contents.content" class="content-content"></div>
      </div>
    </div>
    <div class="ser">
      <div class="xian1"></div>
      <div class="xian2"></div>
      <div class="xian3"></div>
      <h6>SERVICE GUARANTEE</h6>
      <h3>
        服务
        <span>保障</span>
      </h3>
      <ul class="sea">
        <div class="xian"></div>
        <div class="xian"></div>
        <div class="xian"></div>
        <li>
          <img src="@/styles/img/xy3.png" alt />
          <div>优质选材</div>
        </li>
        <li>
          <img src="@/styles/img/xy4.png" alt />
          <div>科技生产</div>
        </li>
        <li>
          <img src="@/styles/img/xy5.png" alt />
          <div>精湛工艺</div>
        </li>
        <li>
          <img src="@/styles/img/xy6.png" alt />
          <div>贴心包装</div>
        </li>
      </ul>
    </div>
    <!-- 内容部分 -->
    <section>
      <goods-list :goods="goodsList" title="推荐商品"></goods-list>
    </section>
    <div class="foo">
      <div class="foo-left">
        <i class="iconfont icon-shoucang"></i>
        <div>收藏</div>
      </div>
      <div class="foo-center" @click="handleCart()">加入购物车</div>
      <div class="foo-right" @click="handleBuyNow">立即购买</div>
    </div>
    <van-sku
      v-model="show"
      :sku="sku"
      :goods="goods"
      :goods-id="gid"
      :quota="0"
      :hide-stock="true"
      @buy-clicked="handelBuyClicked"
      @add-cart="onAddCartClicked"
    />
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import GoodsList from "components/common/goodsList/GoodsList";
import {mapState,mapMutations} from "vuex";
import qs from "qs";
export default {
  name: "Details",
  data() {
    return {
      gid: 0,
      page: 1,
      pageSize: 6,
      contents: {},
      banner: [],
      now: 1,
      goods: {},
      message: [],
      // 颜色
      colorData: [],
      activeColor: "",
      // 推荐列表
      goodsList: [],
      // 配送模板
      templateT: [],
      // 浏览历史商品id
      viewHistory: [],
      // 浏览过的历史商品信息
      viewHistoryGoods: [],
      count: 1, // 购买商品的数量
      // 是否显示
      show: false,
      // 商品的sku数据
      sku: {
        // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
        // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
        tree: [
          {
            k: "颜色", // skuKeyName：规格类目名称
            v: [
              {
                id: "30349", // skuValueId：规格值 id
                name: "红色", // skuValueName：规格值名称
                imgUrl: "", // 规格类目图片，只有第一个规格类目可以定义图片
                previewImgUrl: "" // 用于预览显示的规格类目图片
              },
              {
                id: "1215",
                name: "蓝色",
                imgUrl: "https://img.yzcdn.cn/2.jpg",
                previewImgUrl: "https://img.yzcdn.cn/2p.jpg"
              }
            ],
            k_s: "s1" // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
          }
        ],
        // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
        list: [
          {
            id: 2259, // skuId，下单时后端需要
            price: 100, // 价格（单位分）
            s1: "1215", // 规格类目 k_s 为 s1 的对应规格值 id
            s2: "1193", // 规格类目 k_s 为 s2 的对应规格值 id
            s3: "0", // 最多包含3个规格值，为0表示不存在该规格
            stock_num: 110 // 当前 sku 组合对应的库存
          }
        ],
        price: "1.00", // 默认价格（单位元）
        stock_num: 227, // 商品总库存
        collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
        none_sku: false, // 是否无规格商品
        messages: [
          {
            // 商品留言
            datetime: "0", // 留言类型为 time 时，是否含日期。'1' 表示包含
            multiple: "0", // 留言类型为 text 时，是否多行文本。'1' 表示多行
            name: "留言", // 留言名称
            type: "text", // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
            required: "1", // 是否必填 '1' 表示必填
            placeholder: "" // 可选值，占位文本
          }
        ],
        hide_stock: false // 是否隐藏剩余库存
      },
      // 是否已经添加购物车
      hasAddToCart:false
    };
  },
  computed: {
    ...mapState(['cart'])
  },
  components: {
    NavBar,
    GoodsList
  },
  created() {},
  mounted() {
    this.gid = this.$route.query.gid;
    console.log(this.contents);
    
    // 判断购物车在不在
    let obj =this.cart.find(v=>v.id*1===this.gid);
      if(obj){
        
        this.count = obj.count;
        this.activeColor = obj.activeColor;
        this.hasAddToCart = true;
      }
      
    this.fetchContentData();
    this.fetchGoodsColor();
    this.fetchDetailData();
    this.fetchGoodsData();
    this.fetchTemplate();
    if (localStorage.getItem("viewHistory")) {
      this.viewHistory = JSON.parse(localStorage.getItem("viewHistory")).filter(
        v => v !== this.gid * 1 // 过滤到当前商品
      );
    }
    this.fetchHistory(); // 获取商品浏览查询
  },
  beforeRouteLeave(to, from, next) {
    // 去重
    let index = this.viewHistory.indexOf(this.gid * 1);
    if (index != -1) {
      this.viewHistory.splice(index, 1);
    }
    // 离开页面存储数据
    this.viewHistory.unshift(this.gid * 1);
    localStorage.setItem("viewHistory", JSON.stringify(this.viewHistory));
    next();
  },
  beforeRouteUpdate(to, from, next) {
    // 去重
    let index = this.viewHistory.indexOf(this.gid * 1);
    if (index != -1) {
      this.viewHistory.splice(index, 1);
    }
    // 离开页面存储数据
    this.viewHistory.unshift(this.gid * 1);
    localStorage.setItem("viewHistory", JSON.stringify(this.viewHistory));

    this.banner = [];
    this.message = [];
    this.colorData = [];
    this.goodsList = [];
    this.viewHistory = [];
    this.viewHistoryGoods = [];
    (this.templateT = []), (this.now = 0);
    this.gid = to.query.gid;
    // 初始化的默认
    this.hasAddToCart = false;
    this.fetchContentData();
    this.fetchGoodsColor();
    this.fetchDetailData();
    this.fetchGoodsData();
    this.fetchTemplate();

        // 判断购物车在不在
    let obj =this.cart.find(v=>v.id*1===this.gid);
      if(obj){
        
        this.count = obj.count;
        this.activeColor = obj.activeColor;
        this.hasAddToCart = true;
      }

    if (localStorage.getItem("viewHistory")) {
      this.viewHistory = JSON.parse(localStorage.getItem("viewHistory")).filter(
        v => v !== this.gid * 1 // 过滤到当前商品
      );
    }
    this.fetchHistory(); // 获取商品浏览查询
    this.$nextTick(() => {
      document.documentElement.scrollTop = 0;
    });
    next();
  },
  methods: {
    ...mapMutations(["addToCart","removeFromCart"]),
    // 加入购物车
    handleCart(buyNow=false){
      // this.show = true;
      if(this.activeColor===""){
        this.$toast('请选择颜色');
        return;
      }
      // 如果已经加入购物车 添加会移除旧的数据
      if(this.hasAddToCart){
        this.removeFromCart(this.gid);
      }
      this.hasAddToCart = true;
      let obj ={};
      obj.id = this.gid;
      obj.thumb = this.goods.thumb;
      obj.name = this.goods.name;
      obj.price = this.goods.special_price;
      obj.sku = this.colorData[this.activeColor];
      obj.count = this.count;
      obj.activeColor = this.activeColor;


      obj.tid = this.contents.did;


      if(buyNow){
        obj.checked=true;
      }else{
        obj.checked=false
      }
      this.addToCart(obj)
      this.$toast('添加成功');
    },
    handleBuyNow(){
      this.handleCart();
      this.$router.push({name:"cart"})
    },
    // 购物车
    handelBuyClicked() {},
    onAddCartClicked() {},
    // 商品数量的增减
    handleDec() {
      if (this.count === 1) {
        return;
      }
      this.count--;
    },
    handleAdd() {
      if (this.activeColor === "") {
        this.$toast("请选择商品颜色");
        return;
      }
      if (this.count === this.colorData[this.activeColor].store) {
        this.$toast("当前商品颜色库存量不足");
        return;
      }
      this.count++;
    },
    hadleActiveColor(index) {
      this.activeColor = index;
      this.count = 1;
    },
    // 图片数量的监听
    handleChange(index) {
      this.now = index + 1;
    },
    // 获取数据
    async fetchDetailData() {
      let res = await this.$http.get(this.$api.COLORS, {
        gid: this.gid
      });
      //  console.log(res);
      this.colorData = res.data.data;
    },
    async fetchGoodsColor() {
      let res = await this.$http.get(this.$api.GOODS + "/" + this.gid);
      //  console.log(res);
      this.goods = res.data.data;
    },
    // 获取商品详情
    async fetchContentData() {
      let res = await this.$http.get(this.$api.DETAIL, {
        gid: this.gid
      });
      this.contents = res.data.data[0];
      this.banner = this.contents.banner.split(";");
      this.message = qs.parse(this.contents.message);
      console.log(this.contents);
      
    },
    // 获取推荐商品
    async fetchGoodsData() {
      let res = await this.$http.get(this.$api.GOODS, {
        state: 1,
        page: this.page,
        pageSize: this.pageSize,
        recommend: 1
      });
      // 推荐不能出现现在看的商品
      this.goodsList = res.data.data.filter(v => v.id != this.gid);
    },
    // 获取商品配送模板
    async fetchTemplate() {
      let res = await this.$http.get(this.$api.DDETAIL, {
        did: this.gid,
        page: 1,
        pageSize: 100
      });
      console.log(res);
      this.templateT = res.data.data;
    },
    // 获取浏览历史数据
    fetchHistory() {
      this.viewHistory.forEach(async id => {
        let res = await this.$http.get(this.$api.GOODS + "/" + id);
        this.viewHistoryGoods.push(res.data.data);
        console.log(this.viewHistoryGoods);
      });
    }
  }
};

// 暂时不用
// ,background:activeColor===index ? v.value:''
</script>
<style lang='scss' scoped>
@import "styles/detail";
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
  height: 100%;
  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}
.nav-bar {
  position: fixed;
  top: 0;
  // left: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
}
.header img {
  width: 1.94rem;
  height: 0.4rem;
  margin-top: 0.24rem;
}
.lefts img {
  width: 0.6rem;
  height: 0.4rem;
  position: absolute;
  top: 0.15rem;
  left: 0.1rem;
}
.main-hot-pro li {
  width: 0.26rem;
  height: 0.26rem;
  border-radius: 50%;
  align-self: center;
  display: inline-block;
  margin-left: 0.1rem;
}
.number {
  float: right;
  margin-top: 0.4rem;
  margin-right: 0.3rem;
  color: rgb(255, 123, 0);
  font-weight: 600;
  font-size: 14px;
}
.main-hot-pro li {
  width: 0.26rem;
  height: 0.26rem;
  border-radius: 50%;
  border: 4px solid red;
  align-self: center;
  display: inline-block;
  margin-left: 0.1rem;
}
.content-content {
  margin-top: 0.5rem;
}
</style>
